label{
  display: inline-block;
  width: 75px;
}

input {
  display: inline-block;
  margin-top: 5px;
}

button {
  background-color: rgb(208, 220, 255);
  border-color: rgb(21, 21, 150);
  color:rgb(9, 5, 65);
  padding: 6px 12px;
  display: inline-block;
  font-size: 15px;
  position: relative;
  left: 75px;
  top: 5px;
  width: 170px;
}

button:hover {
  background-color: rgb(113, 149, 255);
}

button:active {
  background-color: rgb(37, 91, 255);
  box-shadow: 0 2px rgb(88, 106, 160);
  transform: translateY(3px);
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
